import routers from './routers/index'
import { useRoutes } from 'react-router-dom'
import { useSelector } from 'react-redux'
import axios from 'axios'
import { useEffect } from 'react'

 function App() {
  //const navigate = useNavigate()
  //const location = useLocation()

  const isUserAuth = useSelector((state) => state.auth.isUser)
  const isKurumAuth = useSelector((state) => state.auth.isKurum)
  const isAdminAuth = useSelector((state) => state.admin.isAdmin)


  //const activeProjectID = useSelector((state) => state.kurum.active_project_id)
  useEffect(() => {
    // Proje seilmemise project sayfasndan teye geme
    /*if(location.pathname.includes('kurum') && kurum && !activeProjectID) {
      navigate('/kurum/project')
    }*/
  }, [])
  
  // rota objesini tutan fonksiyona login durumunu gnderiyor gerisini useRoutes hook' hallediyor
  // eer 2 - style kullanlacaksa router sayfasnda rotalar sadece arrayde tut arguman ve array func ' kaldr
  const routing = useRoutes(routers(isUserAuth, isKurumAuth, isAdminAuth));


  // Kurum-Front-Admin Layoutlarda token header'a geiliyor. token pass olduktan sonra logout olunca silmesi lazm - bu durum sadece component geilerinde var sanrm yani sayfa komple refresh olunca zaten header sfrlanyor aslnda sadece requestlerde gitse yeterli
  if(!isUserAuth && !isKurumAuth && !isAdminAuth) axios.defaults.headers.common['Authorization'] = `Bearer `
  
  return (
    <div className="App">
      {/* WAY BETTER FOR MIDDLEWARE AND CLEAR */}

      {routing}
   
    </div>
  );
}

export default App;
